@import url(https://fonts.googleapis.com/css2?family=Montserrat:wght@400;600;700&family=Rock+Salt&display=swap);
*,
html {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  font-family: "Montserrat", sans-serif;
}
body {
  background: linear-gradient(0deg, #ffdee9, #b5fffc);
}
#header-font {
  font-family: "Rock Salt", cursive;
  font-size: 2rem;
}

#loader {
  display: block;
  position: fixed;
  top: 50%;
  left: 50%;
  -webkit-transform: translate(-50%, -50%);
          transform: translate(-50%, -50%);
}
.main-container {
  width: 100vw;
  height: 85vh;
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: center;
          justify-content: center;
  -webkit-align-items: center;
          align-items: center;
}





